import {
	mapState,
	mapMutations
} from 'vuex';
import productList from '@/components/productList.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import interfaces from '@/utils/interfaces.js'
export default {
	components: {
		productList,
		uniPopup,
	},
	computed: mapState(['hasLogin', 'uerInfo']),
	data() {
		return {
			title: 'getUserInfo',
			hasUserInfo: false,
			userInfo: {},
			ishow: true,
			currentPage: '/pages/index/index',
			swiperList: [{
					id: 1,
					img: "/static/images/banner.jpg"
				},
				{
					id: 2,
					img: "/static/images/banner.jpg"
				},
				{
					id: 3,
					img: "/static/images/banner.jpg"
				}
			], //轮播图片
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			type: 5, //1 发布采购 2 发布销售 3 委托销售 4 委托采购 5 官网销售 不传返回所有
			pageSize: 4, //分页大小
			pageNum: 1, //页码
			cate_id: "",
			defalutSort: "", //是否综合排序 0 否 1 是 如果为 1
			priceSort: "", //价格排序 asc 升序 desc 降序
			timeSort: "", //剩余时间排序 asc 升序 desc 降序
			loadingText: "正在加载....",
			categoryList: [],
			publishList: [{
					title: "发布采购",
					bgimg: "/static/images/release01.png",
					icon: "/static/images/releaseicon01.png",
					url: "/pages/publish/publishBuy/publishOne?type=1"
				},
				{
					title: "发布销售",
					bgimg: "/static/images/release02.png",
					icon: "/static/images/releaseicon02.png",
					url: "/pages/publish/publishBuy/publishOne?type=2"
				},
				{
					title: "发布委托",
					bgimg: "/static/images/release03.png",
					icon: "/static/images/releaseicon03.png",
					url: "/pages/publish/publishBuy/publishOne?type=3"
				}
			], //发布
			goodsList: [],
			firstImages: '',
			filterByList: [{
					cntitle: "列表推荐",
					entitle: "Recommend",
					selected: true,
					type: "5"
				},
				{
					cntitle: "采购订单",
					entitle: "Purchase",
					selected: false,
					type: "1"
				},
				{
					cntitle: "销售订单",
					entitle: "Sale",
					selected: false,
					type: "2"
				},
				{
					cntitle: "委托订单",
					entitle: "Entrust",
					selected: false,
					type: "3,4"
				},
			] //列表tabbar
		}


	},

	methods: {
		handleCategory(item) {
			// 分类跳转
			// console.log(item.text);
			//console.log(item);
			uni.navigateTo({
				url: "../product/productList?id=" + item.id + '&name=' + item.cn_name
			})
		},
		handleSelect(index, target) {
			this.filterByList[index].selected = true;

			// 其他的selected false
			for (let i = 0; i < this.filterByList.length; i++) {
				if (i != index) {
					this.filterByList[i].selected = false;
				}
			}
			this.type = target.type;
			console.log(this.type);
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
		},
		initData() {
			this.request({ //分类筛选
				url: interfaces.getkindData,
				dataType: "JSON",
				method: 'POST', //请求方式
				success: (res) => {
					//console.log(res);
					this.categoryList = res.data;
				}
			});
		},
		loadData() {
			//debugger
			let params = {
				data: {
					page_size: this.pageSize,
					page_index: this.pageNum,
					keyword: "",
					type: this.type,
					is_defalut_sort: "",
					status: "",
					price_sort: "",
					remain_time_sort: "",
					cate_id: "",
					brand_id: ""
				}
			}
			console.log(params);
			this.request({
				url: interfaces.getNeedsData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: params,
				success: (res) => {
					if (res.code == 200) {
						let lists = res.data.list;
						console.log(lists);
						for (let i = 0; i < lists.length; i++) { //转成数组
							let serviceData =lists[i].service_cnname !=null?lists[i].service_cnname.split(','):'';
							lists[i].service_cnname = serviceData;
							//console.log('222',serviceData);
							let qualification = lists[i].qualification_icon !=null?lists[i].qualification_icon.split(','):'';
							lists[i].qualification_icon = qualification;
						}
						if (lists.length < this.pageSize) {
							this.loadingText = "到底了";
						}
						if (lists.length > 0) {
							lists.forEach(item => {
								this.goodsList.push(item);
							})
						} else {
							this.loadingText = "到底了";
						}
					}
				}
			});
		},
		handlePublish(item) {

			uni.navigateTo({
				url: item.url
			})
		},
		enterRegister() { //注册
			uni.navigateTo({
				url: "/pages/user/registerEnter"
			})
		},
		enterLogin() {
			uni.navigateTo({
				url: "/pages/user/login"
			})
		},
		//判断是否登录状态
		...mapMutations(['logout']),
		bindLogin() {
			if (this.hasLogin) {
				this.logout();
				this.ishow = true;
			} else {
				uni.reLaunch({
					url: '/pages/user/login'
				})
			}
		}
	},
	onLoad() {
		this.initData();
		this.loadData();
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			uni.stopPullDownRefresh();
		}, 1000)
	},
	// 上拉加载
	onReachBottom() {
		//debugger
		this.pageNum++;
		this.loadData();
	}
}
